const CONTENT = [
    { id: 1, title: " ", dec: "  ", link: "/a" },
    { id: 2, title: " ", dec: "  ", link: "/a" },
    { id: 3, title: " ", dec: "  ", link: "/a" },
    { id: 4, title: " ", dec: ",  ", link: "/a" },
]

const MainContent = () => {
    return (
        <section className="MainContent csc">
            <h2>  ,  </h2>
            <p>      .</p>
            <div className="inner">
                {
                    CONTENT.map(content => {
                        return (
                            <figure key={content.id}>
                                <div className="box">
                                    <img src={process.env.PUBLIC_URL + "/assets/images/section1_" + content.id + ".PNG"} alt="" />
                                </div>
                                <div className="tit">{content.title}</div>
                                <div className="dec">{content.dec}</div>
                                <a href={content.link} className="cbtn"> </a>
                            </figure>
                        )
                    })
                }
            </div>
        </section>
    )
}
export default MainContent;