import React from 'react';


const Article = ({ content, num }) => {
    return (
        <article className='Article'>
            <div className="tit">
                <strong>{content[num].tit}</strong>&nbsp;&nbsp; / &nbsp;&nbsp;{content[1].tit}
            </div>
            <div className='bg'>
                <div className="case">
                    <div className="tit__log">
                        <img src={process.env.PUBLIC_URL + '/assets/images/.png'} alt="" />
                    </div>
                    <h2>
                        {content[num].tit}
                    </h2>
                    <p>
                        {content[num].con}
                    </p>
                </div>
            </div>


            <div className="sub__tit">
                  
            </div>


            <table summary=" (A, B, C,D)" className="tb_org02 mt10">
                <colgroup>
                    <col style={{ width: '94px' }} />
                    <col style={{ width: '94px' }} />
                    <col style={{ width: '94px' }} />
                    <col style={{ width: '94px' }} />
                    <col style={{ width: '200px' }} />

                </colgroup>


                <thead>
                    <tr>
                        <th scope="col">
                        </th>
                        <th scope="col">
                            A
                        </th>
                        <th scope="col">
                            B
                        </th>
                        <th scope="col">
                            C
                        </th>
                        <th scope="col">
                            D
                        </th>
                        <th scope="col" className="last"></th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>/</td>
                        <td>
                            58,000 
                        </td>
                        <td>
                            50,000 
                        </td>
                        <td>
                            46,000 
                        </td>
                        <td>
                            64,000 
                        </td>
                        <td rowspan="2" className="last alignL bdOrg">
                            <ul className="td_listL">
                                <li>    <br /> (//   <br />  )</li>
                            </ul>
                        </td>
                    </tr>
                    <tr>
                        <td className="bdOrg">/</td>
                        <td className="bdOrg">
                            46,000 
                        </td>
                        <td className="bdOrg">
                            40,000 
                        </td>
                        <td className="bdOrg">
                            36,000 
                        </td>
                        <td className="bdOrg">
                            51,000 
                        </td>
                    </tr>
                </tbody>
            </table>



            <div className="sub__tit">
                 
            </div>
            <p className="sub__des">
                {content[num].des}
            </p>

            <div className="sub__img">
                <figure>
                    <img src={process.env.PUBLIC_URL + '/assets/images/article02_1.png'} alt="" />
                </figure>
                <figure>
                    <img src={process.env.PUBLIC_URL + '/assets/images/article02_2.png'} alt="" />
                </figure>
                <figure>
                    <img src={process.env.PUBLIC_URL + '/assets/images/article02_3.png'} alt="" />
                </figure>
                <figure>
                    <img src={process.env.PUBLIC_URL + '/assets/images/article02_4.png'} alt="" />
                </figure>
            </div>

            <div className="sub__tit">
                 
            </div>

            <ul className="sub__service">

                <li >   (      )
                    <ul className='txt'>
                        <li>-  :  65 ~</li>
                        <li>-  :  13 ~  18</li>
                        <li>-  : 36 ~  12</li>
                        <li>- 36  :
                              (     )</li>
                    </ul>
                </li>

                <li>  ( )
                    <ul className='txt'>
                        <li>-    <br />
                             :  <br />
                             :   (1)</li>
                        <li>-     <br />
                             :  <br />
                                
                        </li>

                    </ul></li>
                <li>  ( )
                    <ul className='txt'>
                        <li>-  :  50%   </li>
                        <li>-  :  50%    ( 1)</li>
                    </ul></li>

                <li>3    (,   3    )
                    <ul className='txt'>
                        <li>-     </li>
                        <li>-   :    15  3  3    </li>
                        <li>-         </li>

                    </ul></li>
                <li>  (   )
                    <ul className='txt'>
                        <li>- ( )   </li>
                    </ul></li>

            </ul>

        </article>
    )
}

export default Article;