import { useEffect, useState } from "react"

const FLINK = [
    { id: 0, name: "Family Link", link: "" },
    { id: 1, name: " ", link: "http://rnc.samsungcnt.com/" },
    { id: 2, name: " ", link: "https://www.golfsamsung.com/web/plugins.do" },
    { id: 3, name: "  ", link: "https://www.guidedog.co.kr/" },
    { id: 4, name: " ", link: "http://www.evercs.com/" },
]

const FamilyLink = () => {
    const [flnk, setflnk] = useState();

    useEffect(() => {
        flnk && window.open(flnk)
    }, [flnk])

    return (
        <select onChange={(e) => setflnk(e.target.value)}>
            {
                FLINK.map(link => <option value={link.link} key={link.id}>{link.name}</option>)
            }
        </select>
    )
}

export default FamilyLink;