const NEWS = [
    { id: 1, category: " ", title: "PICK-UP ", date: " ", link: "/", src: "/assets/images/main_news01.jpg" },
    { id: 2, category: " ", title: "  ", date: "   ", link: "/", src: "/assets/images/main_news02.jpg" },
    { id: 3, category: " ", title: "", date: "  .", link: "/", src: "/assets/images/main_news03.jpg" },
    { id: 4, category: " ", title: " ", date: "  ", link: "/", src: "/assets/images/main_news04.jpg" },
    { id: 5, category: " ", title: "   ", date: "  ", link: "/", src: "/assets/images/main_news05.jpg" },
    { id: 6, category: "", title: "  +  ", date: " OPEN", link: "/", src: "/assets/images/main_news06.jpg" },
    { id: 7, category: "", title: "  ", date: " X ", link: "/", src: "/assets/images/main_news07.jpg" },
    { id: 8, category: "", title: " ", date: "  ", link: "/", src: "/assets/images/main_news08.jpg" },
]


const News = () => {
    return (
        <section className="News csc">
            <h2> </h2>
            <p>  .</p>
            <div className="inner">
                {
                    NEWS.map(news => {
                        return (
                            <figure key={news.id}>
                                <a href={news.link}>
                                    <div className="box">
                                        <img src={process.env.PUBLIC_URL + news.src} alt={news.title} />
                                    </div>
                                    <strong>{news.category}</strong>
                                    <div className="title">{news.title}</div>
                                    <span>{news.date}</span>
                                </a>
                            </figure>
                        )
                    })
                }
            </div>
        </section>
    )
}
export default News;